<template>
  <div v-if="showImgDialog" @touchmove.prevent class="dialog">
    <div class="dialog__inner">
      <div class="dialog__content">
        <swiper v-if="imgList.length" :options="swiperOption" class="swiper-container">
          <swiper-slide v-for="(item, index) in imgList" :key="index">
            <div class="dialog__img-wrapper">
              <img v-lazy="$useWebpImg(item.imgUrl)"
                   :class="[item.coverHeight?'cover-height':'cover-width']"
                   class="dialog__img" src="" alt="">
              <div @click="openVideoDialog(item)" v-if="item.video" class="dialog__icon-video"></div>
            </div>
          </swiper-slide>
          <div class="dialog__pagination swiper-pagination" slot="pagination"></div>
          <button class="dialog__prev swiper-button-prev" slot="button-prev"></button>
          <button class="dialog__next swiper-button-next" slot="button-next"></button>
        </swiper>
        <a @click="$emit('update:showImgDialog',false)" class="dialog__close" href="javascript:void (0);"></a>
      </div>
    </div>
    <VideoDialog :showVideoDialog.sync="showVideoDialog" :video="video" :poster="poster"></VideoDialog>
  </div>
</template>

<script>
import VideoDialog from '@/components/dialog/VideoDialog'

export default {
  components: { VideoDialog },
  name: 'ImgDialog',
  props: {
    showImgDialog: Boolean,
    imgList: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      swiperOption: {
        autoplay: {
          delay: 3000
        },
        pagination: {
          el: '.swiper-pagination',
          dynamicBullets: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }

      },
      video: null,
      poster: null,
      showVideoDialog: false
    }
  },
  methods: {
    openVideoDialog(item) {
      this.video = item.video
      this.poster = this.$useWebpImg(item.imgUrl)
      this.showVideoDialog = true
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "playDialog.scss";

  .dialog {
    &__img-wrapper {
      position: relative;
      height: 100%;
      overflow: hidden;
      transform-style: preserve-3d;
    }

    &__img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;

      &.cover-height {
        width: auto;
        height: 100%;
      }

      &.cover-width {
        width: 100%;
        height: auto;
      }
    }

    &__prev,
    &__next {
      position: absolute;
      top: 50%;
      width: rem(34);
      height: rem(34);
      margin-top: rem(-17);
      border: none;
      outline: none;
      transform: translateZ(0);
    }

    &__prev {
      left: 0;
      background: url(~@/assets/img/components/dialog/prev.png) no-repeat;
      background-size: 100% auto;
    }

    &__next {
      right: 0;
      background: url(~@/assets/img/components/dialog/next.png) no-repeat;
      background-size: 100% auto;
    }

    &__pagination {
      transform: translate3d(-50%, 0, 0) !important;
    }
  }

  .swiper-container {
    height: 100%;
  }
</style>
