<template>
  <div v-if="showVideoDialog" @touchmove.prevent class="dialog">
    <div class="dialog__inner">
      <div class="dialog__content">
        <video :poster="poster" autoplay controls="controls" width="100%" height="100%">
          <source :src="videoUrl" type="video/mp4">
        </video>
        <a @click="$emit('update:showVideoDialog',false)" class="dialog__close" href="javascript:void (0);"></a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VideoDialog',
  props: {
    video: String,
    poster: String,
    showVideoDialog: Boolean
  },
  data() {
    return {}
  },
  computed: {
    videoUrl() {
      return 'http://vod.yzbabyu.com/applications/A02/streams/_definst_/' + this.video
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "playDialog.scss";

  video {
    position: relative;
    z-index: 2;
  }
</style>
